.main{
    background-color: darkslategray;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;

}

.menu{
    display: flex;
    justify-content: space-between;
}

 a{
    text-decoration: none;
    padding: 5px;
    color:white;
    border-bottom: 1px solid white;
    transition: all 0.2s ease-in-out;
}

a:hover{
    color: blue;
    border-color: blue;
}
.activeNavLink{
    color: blue;
    border-color: blue;
}
